}
.wrapper nav{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.wrapper .items{
  display: -webkit-flex;
  display: flex;
  max-width: 720px;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.items span{
  padding: 7px 25px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  color: black;     /* colour of text inside the box buttons*/
  border-radius: 50px;
  border: 2px solid #007bff;   /* border of buttons */
  transition: all 0.3s ease;
}
.items span.active,
.items span:hover{
  color: black;
  background: red;   /*background colour of all button */
}

.gallery{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 30px;
}
.gallery .image{
  width: calc(100% / 4);
  padding: 7px;
}
.gallery .image span{
  display: -webkit-flex;
  display: flex;
  width: 100%;
  overflow: hidden;
}
.gallery .image img{
  width: 100%;
  vertical-align: middle;
  transition: all 0.3s ease;
}
.gallery .image:hover img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.gallery .image.hide{
  display: none;
}
.gallery .image.show{
  -webkit-animation: animate 0.4s ease;
          animation: animate 0.4s ease;
}
@-webkit-keyframes animate {
  0%{
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes animate {
  0%{
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.preview-box{
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0.9);
          transform: translate(-50%, -50%) scale(0.9);
  background: #fff;
  max-width: 700px;
  width: 100%;
  z-index: 5;
  opacity: 0;
  pointer-events: none;
  border-radius: 3px;
  padding: 0 5px 5px 5px;
  box-shadow: 0px 0px 15px rgba(0,0,0,0.2);
}
.preview-box.show{
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  transition: all 0.3s ease;
}
.preview-box .details{
  padding: 13px 15px 13px 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.details .title{
